import { useState } from "react"

const UsersModalLoadCSV = ({ active, setActive }) => {
    return (
        <div className={active ? "modalContainer active" : "modalContainer"} onClick={() => setActive(false)}>
            <div className="modalContent" onClick={e => e.stopPropagation()}>
                <h2>Импортирование пользователя из файла</h2>
                <hr></hr>
                <div className="row">
                    <div className="col-md-10 col-12">
                        <p>Выберите csv файл с пользователями, где каждая строка файла будет типа "логин;почта;пароль;роль".
                            Пример: "ivan; ivan@example.ru;123"</p>
                    </div>
                    <div className="col-md-2 col-12">
                        <button type="button" className="buttonFill" style={{marginTop: "20px"}}>Выбрать файл</button>
                    </div>
                </div>

                <hr style={{ marginTop: "20px" }}></hr>
                <div className="navigationTableGroup">
                    <button type="button" style={{ marginRight: "10px" }} className="buttonWithoutFill">Отменить</button>
                    <button type="button" className="buttonFill" style={{ marginRight: "10px" }}>Сохранить</button>
                </div>
            </div>
        </div>
    )
}
export default UsersModalLoadCSV