import { useState } from "react"

const UsersModal = ({ active, setActive }) => {
    return (
        <div className={active ? "modalContainer active" : "modalContainer"} onClick={() => setActive(false)}>
            <div className="modalContent" onClick={e => e.stopPropagation()}>
                <h2>Добавление пользователя</h2>
                <hr></hr>
                <div className="row">
                    <div className="col-12">
                        <p>ФИО</p>
                        <input type='text' />
                    </div>
                    <div className="col-md-6 col-12">
                        <p>Логин</p>
                        <input type='text' />
                    </div>
                    <div className="col-md-6 col-12">
                        <p>Почта</p>
                        <input type='text' />
                    </div>
                    <div className="col-md-4 col-12">
                        <p>Роль</p>
                        <select>
                            <option value="-1" >Все</option>
                            <option value="2">АЙ ГАГАГА</option>
                        </select>
                    </div>
                    <div className="col-md-4 col-12">
                        <p>Пароль</p>
                        <input type='password' />
                    </div>
                    <div className="col-md-4 col-12">
                        <p>Повторите пароль</p>
                        <input type='password' />
                    </div>
                </div>

                <hr style={{ marginTop: "20px" }}></hr>
                <div className="navigationTableGroup">
                    <button type="button" style={{ marginRight: "10px" }} className="buttonWithoutFill">Отменить</button>
                    <button type="button" className="buttonFill" style={{ marginRight: "10px" }}>Сохранить</button>
                </div>
            </div>
        </div>
    )
}
export default UsersModal